<template>
	<view class="shopDetils_con">
		<view class="tabList justify-content-between" v-if="fixedFlag">
			<view class="cow" :class="tabId == item.id?'black':''" v-for="(item, index) in tabList" :key="index" @tap="changeTab(item.id)">{{item.name}}</view>
		</view>
		<scroll-view scroll-y class="scrollBox" :scroll-into-view="toView" :scroll-with-animation="true" :style="{ height: scrollH + 'px' }" @scroll="scroll" :show-scrollbar="false" :class="fixedFlag?'paddingTop':''">
			
			<image :src="resUrl" mode="" class="masterImg" id="zero">
			</image>
			<view class="shop">
				<!-- 名字 面积 分享 -->
				<view class="name">
					<view class="left">
						<view class="tit">
							{{title}}
						</view>
						<view class="size">
							<span>{{rent}}</span>
						</view>
						<view class="tag">
							<span v-for="(tag,i) in topTagList" :key="i">{{tag.title}}</span>
							
						</view>
					</view>
					<button data-name="shareBtn" open-type="share" class="right" plain="true">
						<image src="../../static/icon/fenxiang.png" mode=""></image>
						<view>分享</view>
					</button>
				</view>
				<!-- 物业类型 楼层 开业时间 -->
				<view class="type justify-content-between">
					<view>
						<view class="t">建筑面积</view>
						<view class="b">{{floorSpace}}㎡</view>
					</view>
					<view>
						<view class="t">实际面积</view>
						<view class="b">{{trueSpace}}㎡</view>
					</view>
					<view>
						<view class="t">物业类型</view>
						<view class="b">{{type}}</view>
					</view>
					<view>
						<view class="t">起租期</view>
						<view class="b">{{tenancy}}个月</view>
					</view>
				</view>
				<!-- 营业时间 地址 -->
				<view class="time">
					<view>
						<text class="grey">所属商业：</text>
						<text>{{streetTitle}}</text>
					</view>
					<view>
						<text class="grey">项目地址：</text>
						<text class="d">{{address}}</text>
						<view>
							<view @tap="toMap">
								<image src="../../static/icon/chaxunditu.png" mode=""></image>
								<span>查看地图</span>
							</view>

						</view>

					</view>
				</view>
			</view>
			<!-- 配套设施 -->
			<view class="ptss" id="one">
				<view class="tit flex">
					<view class="xian"></view>
					<view class="t">配套设施</view>
				</view>
				<view class="flex list">
					<view class="items flex" v-for="(item, index) in tagList" :key="index">
						<image :src="item.icon" mode=""></image>
						<view>{{item.title}}</view>
					</view>
					<!-- <view class="items flex">
						<image src="../../static/facility/wangluo.png" mode=""></image>
						<view class="line">网络</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/nuanqi.png" mode=""></image>
						<view>暖气</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/trq.png" mode=""></image>
						<view>天然气</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/shangshui.png" mode=""></image>
						<view>上水</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/xiashui.png" mode=""></image>
						<view>下水</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/paiyan.png" mode=""></image>
						<view>排烟</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/paiwu.png" mode=""></image>
						<view>排污</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/guanmei.png" mode=""></image>
						<view>管煤</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/tcw.png" mode=""></image>
						<view>停车位</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/380v.png" mode=""></image>
						<view>380v</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/futi.png" mode=""></image>
						<view>扶梯</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/ht.png" mode=""></image>
						<view>货梯</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/keti.png" mode=""></image>
						<view>客梯</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/zykt.png" mode=""></image>
						<view>中央空调</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/wbq.png" mode=""></image>
						<view>外摆区</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/kmh.png" mode=""></image>
						<view>可明火</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/gzmq.png" mode=""></image>
						<view>罐装煤气</view>
					</view>
					<view class="items flex">
						<image src="../../static/facility/dianliang.png" mode=""></image>
						<view>电量6KW</view>
					</view> -->
				</view>
			</view>
			<!-- 商铺描述info -->
			<view class="spms" id="two">
				<view class="tit flex">
					<view class="xian"></view>
					<view class="t">商铺描述</view>
				</view>
				<view class="des">
					{{info}}
				</view>
			</view>
			<!-- 商铺图info -->
			<view class="spt" id="thr">
				<view class="tit flex">
					<view class="xian"></view>
					<view class="t">商铺图</view>
				</view>
				<!-- 删掉文字 只留图 -->
				<view>{{info}}</view>
				<image src="../../static/blue.png" mode=""></image>
				<image src="../../static/blue.png" mode=""></image>
			</view>

			<!-- 位置及周边 -->
			<view class="wzjzb" id="four">
				<view class="tit flex">
					<view class="xian"></view>
					<view class="t">位置及周边</view>
				</view>
				<map id="map" :longitude="lng" :latitude="lat" scale="15" :markers="markers" show-location style="width: 100%; height: 380rpx; margin-top:30rpx;" :enable-zoom="false" :enable-scroll="false"></map>
				<view class="traffic flex">
					<view class="tit flex">
						<view v-for="(item, index) in trafficList" :key="index">
							<text :class="trafficId == item.id?'select':''" @tap="changeTraffic(item.id)">{{item.name}}</text>
						</view>
					</view>

				</view>
				<!-- 地铁站 公交站 教育 小区 -->
				<view class="fenlei">
					<view v-for="(item, index) in trafficItem" :key="index">{{item}}</view>
				</view>
				<view class="kuang">{{showAddress}}</view>
			</view>
			
		</scroll-view>
		<view class="bottom flex">
			<view class="flex">
				<button class="kefu" open-type="contact" style="line-height: 1;" @bindcontact="handleContact">
					<image src="../../static/icon/kefu.png" mode=""></image>
					<view>客服</view>
				</button>
				<view class="shoucang" @tap="changeCollect">
					<image src="../../static/icon/shoucang.png" mode="" v-if="isCollect == 0"></image>
					<image src="../../static/icon/shoucang_select.png" mode="" v-if="isCollect == 1"></image>
					<view>收藏</view>
				</view>
			</view>
			<view class="zhaoshang" @tap="attractCall">招商热线</view>
			<view class="yuyue" @tap="order">预约</view>
		</view>
	</view>
</template>

<script>
	import QQMapWX from '@/common/qqmap-wx-jssdk.min.js'
	var qqmapsdk;
	export default {
		
		onLoad(res) {
			qqmapsdk = new QQMapWX({
				key: 'PACBZ-A5HWR-OWLWK-WPDMW-LKO62-EVBMD'
			});
			this.userId = uni.getStorageSync('userInfo').userId
			this.token = uni.getStorageSync('userInfo').token
			uni.setNavigationBarTitle({
				title: '920商铺'
			});
			this.storeId = res.storeId
			// this.storeId = 1
			this.getDetails()
		},
		onReady() {
			uni.getSystemInfo({
				success: (res)=> {
					this.scrollH = res.windowHeight 
				}
			});
		},
		data() {
			return {
				scrollH: 0,
				markers: [],
				trafficList: [{
					name: '地铁站',
					id: 0
				}, {
					name: '公交站',
					id: 1
				}, {
					name: '教育',
					id: 2
				}, {
					name: '小区',
					id: 3
				}],
				trafficId: 0,
				// 下滑出现的导航头
				tabList: [{
						name: '详情',
						id: 0
					},
					{
						name: '配套',
						id: 1
					},
					{
						name: '描述',
						id: 2
					},
					{
						name: '商铺图',
						id: 3
					},
					{
						name: '位置',
						id: 4
					}
				],
				tabId: 0,
				phone: '0000000',
				fixedFlag: false,
				toView: '',
				userId: '',
				storeId: '',
				token: '',
				headline: '920商铺',
				resUrl:'',
				title:'',
				showAddress:'',
				rent:'',
				topTagList:[],
				floorSpace:'',
				trueSpace:'',
				type:'',
				tenancy:'',
				address:'',
				tagList:[],
				info: '',
				// 商铺图
				lat:'',
				lng:'',
				mobile:'',
				mobile2:'',
				isCollect: '',
				trafficItem:[],
				trafficFlag: false

			}
		},
		methods: {
			// 客服的回调
			handleContact(e){
				console.log(e)
			},
			scroll(e){
				if (e.detail.scrollTop > 500) {
					this.fixedFlag = true
				} else {
					this.fixedFlag = false
				}
			},
			toMap() {
				// 要传店铺的经纬度
				uni.navigateTo({
					url: '../map/map?lat=' + this.lat + '&lng=' + this.lng
				})
			},
			// 下滑出现的导航栏
			changeTab(id) {
				this.tabId = id
				if (id == 0) {
					this.toView = 'zero'
				} else if (id == 1) {
					this.toView = 'one'
				} else if (id == 2) {
					this.toView = 'two'
				} else if (id == 3) {
					this.toView = 'thr'
				} else if (id == 4) {
					this.toView = 'four'
				}
			},
			changeTraffic(id) {
				if(this.trafficFlag) return
				this.trafficFlag = true
				this.trafficId = id
				if(this.trafficId == 0){
					qqmapsdk.search({
						keyword: '地铁站',
						location: {
							latitude: this.lat,
							longitude: this.lng
						},
						page_size: 3,
						// filter: '大学',
						success :(res)=> {
							this.trafficItem = []
							res.data.map(r=>{
								this.trafficItem.push(r.address+r.title)
							})
							console.log(res.data);
							this.trafficFlag = false
						},
						fail:(res)=> {
							this.trafficFlag = false
							console.log(res);
						},
					});
				}else if(this.trafficId == 1){
					// 公交车站
					qqmapsdk.search({
						keyword: '公交站',
						location: {
							latitude: this.lat,
							longitude: this.lng
						},
						page_size: 1,
						success :(res)=> {
							this.trafficItem = []
							res.data.map(r=>{
								this.trafficItem.push(r.address)
							})
							this.trafficFlag = false
						},
						fail:(res)=> {
							this.trafficFlag = false
						},
					});
				}else if(this.trafficId == 2){
					// 教育
					qqmapsdk.search({
						keyword: '大学',
						location: {
							latitude: this.lat,
							longitude: this.lng
						},
						page_size: 5,
						// filter: '大学',
						success :(res)=> {
							this.trafficItem = []
							res.data.map(r=>{
								this.trafficItem.push(r.title)
							})
							this.trafficFlag = false
						},
						fail:(res)=> {
							this.trafficFlag = false
						},
					});
				}else if(this.trafficId == 3){
					// 小区
					qqmapsdk.search({
						keyword: '小区',
						location: {
							latitude: this.lat,
							longitude: this.lng
						},
						page_size: 10,
						// filter: '大学',
						success :(res)=> {
							this.trafficItem = []
							res.data.map(r=>{
								this.trafficItem.push(r.title)
							})
							this.trafficFlag = false
						},
						fail:(res)=> {
							this.trafficFlag = false
						},
					});
				}
				
			},
			// 获取详情
			getDetails() {
				let params = {
					userId: this.userId,
					storeId: this.storeId
				}
				uni.showLoading({
					title: '加载中'
				});
				this.$http.post(this.$api.inRentDetils, {
					data: params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res => {
					if (res.data.code == 0) {
						this.resUrl = res.data.data.resUrl
						this.showAddress = res.data.data.showAddress
						this.rent = res.data.data.rent
						this.topTagList = res.data.data.topTagList
						this.floorSpace = res.data.data.floorSpace
						this.trueSpace = res.data.data.trueSpace
						this.type = res.data.data.type
						this.tenancy = res.data.data.tenancy
						this.streetTitle = res.data.data.streetTitle
						this.address = res.data.data.address
						this.tagList = res.data.data.tagList
						this.info = res.data.data.info
						this.lat = res.data.data.lat
						this.lng = res.data.data.lng
						this.isCollect = res.data.data.isCollect
						this.headline = res.data.data.title
						this.mobile = res.data.data.mobile
						this.mobile2 = res.data.data.mobile_2
						uni.setNavigationBarTitle({
							title: this.headline
						});
						this.markers = [{
							id: 1,
							iconPath: "../../static/icon/dingwei.png",
							latitude: this.lat,
							longitude: this.lng,
							width: 17.5,
							height: 25,
							callout: {
								content: '拖动地图精准定位门店',
								borderRadius: 20,
								fontSize: 20
							}
						}]
					
						qqmapsdk.search({
							keyword: '地铁站',
							location: {
								latitude: this.lat,
								longitude: this.lng
							},
							page_size: 3,
							// filter: '大学',
							success :(res)=> {
								this.trafficItem = []
								res.data.map(r=>{
									this.trafficItem.push(r.address+r.title)
								})
							},
							fail:(res)=> {
							},
						});
						uni.hideLoading()
					}else if(res.data.code == 403){
						let details = {
							details: 2,
							storeId: this.storeId
						}
						uni.setStorageSync('details', details)
						uni.switchTab({
							url: '/pages/mine/mine'
						})
					}else{
						this.toast(res.data.msg)
					}
				})
			},
			// 收藏
			changeCollect() {
				if (this.isCollectFlag) return
				this.isCollectFlag = true
				let params = {
					objectId: this.storeId,
					objectType: 1,
					userId: this.userId
				}
				uni.showLoading({
					title: '操作中'
				});
				this.$http.post(this.$api.isCollectChange, {
					data: params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res => {
					if (res.data.code == 0) {
						if (this.isCollect == 0) {
							this.isCollect = 1
							this.toast('收藏成功')
			
						} else {
							this.isCollect = 0
							this.toast('已取消收藏')
						}
						uni.hideLoading()
						this.isCollectFlag = false
					} else {
						this.isCollectFlag = false
						this.toast(res.data.msg)
					}
				})
			},
			// 打电话
			callKefu(){
				uni.makePhoneCall({
					phoneNumber: this.mobile2,
				});
			},
			attractCall(){
				uni.makePhoneCall({
					phoneNumber: this.mobile
				});
			},
			// 预约
			order() {
				uni.navigateTo({
					url:'../appointment/appointment?storeId='+this.storeId
				})
			}
		}
	}
</script>

<style lang="less" scoped>
	.shopDetils_con {
		width: 750rpx;
		background: #F9F9F9;

		.tabList {
			background: #FFFFFF;
			box-shadow: 0rpx 0rpx 11rpx 3rpx rgba(4, 0, 0, 0.06);
			z-index: 9999;
			width: 100%;
			padding: 30rpx 40rpx;
			box-sizing: border-box;
			position: fixed;
			top: 0;

			.cow {
				font-size: 28rpx;
				font-family: PingFang SC;
				font-weight: 400;
				color: #666666;
			}

			.black {
				font-weight: 500;
				color: #343434;
			}
		}

		.scrollBox {
			// height: 3785rpx;
			padding-bottom: 120rpx;
			box-sizing:border-box;
		}
		.paddingTop{
			padding-top: 90rpx;
			box-sizing: border-box;
		}

		.masterImg {
			width: 750rpx;
			height: 500rpx;
		}

		.shop,
		.ptss,
		.spms,
		.spt,
		.wzjzb {
			width: 100%;
			background: #FFFFFF;
			padding: 36rpx;
			box-sizing: border-box;
		}

		.shop {
			// padding-top: 30rpx;
			box-sizing: border-box;

			.name {
				display: flex;
				justify-content: space-between;
				border-bottom: 2rpx solid rgba(238, 238, 238, 1);
				padding-bottom: 28rpx;
				box-sizing: border-box;

				.left {
					.tit {

						font-size: 34rpx;
						font-family: PingFang SC;
						font-weight: 600;
						color: #343434;
					}

					.size {
						margin-top: 30rpx;

						span {
							font-size: 28rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #FF6633;
						}
					}

					.tag {
						margin-top: 35rpx;

						span {
							margin-right: 10rpx;
							border: 2rpx solid #302E38;
							border-radius: 8rpx;
							padding: 9rpx 11rpx;
							box-sizing: border-box;
							font-size: 20rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #302E38;
						}
					}
				}

				.right {
					border: 0;
					border-radius: 0;
					text-align: center;
					line-height: 1;
					image {
						width: 36rpx;
						height: 36rpx;
					}

					view {
						font-size: 22rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #909090;
					}
				}
			}

			.type {
				padding: 42rpx 0;
				box-sizing: border-box;
				border-bottom: 2rpx solid rgba(238, 238, 238, 1);

				>view {
					text-align: center;

					.t {

						font-size: 24rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #909090;
					}

					.b {
						margin-top: 24rpx;
						font-size: 32rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #343434;
					}
				}
			}

			.time {
				>view {
					margin-top: 34rpx;

					text {
						font-size: 24rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: rgba(52, 52, 52, 1);
					}

					.grey {
						color: rgba(144, 144, 144, 1);
					}

					>view {

						display: inline-block;
						margin-left: 18rpx;

						view {
							display: flex;
							align-items: center;
						}

					}

					image {
						width: 26rpx;
						height: 24rpx;
						display: inline-block;
					}

					span {
						margin-left: 6rpx;
						font-size: 24rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #FF6633;
					}
					.d{
						line-height: 36rpx;
					}
				}
			}
		}

		.ptss,
		.spms,
		.spt,
		.wzjzb {
			margin-top: 30rpx;
			padding: 38rpx;
			box-sizing: border-box;

